@import "sass/index";

.wrapper {
  width: 100%;
}
.innerWrapper {
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(147px, 1fr));
  @media (max-width: 600px) {
    grid-template-columns: repeat(auto-fill, minmax(125px, 1fr));
  }
}
.block {
  display: flex;
  flex-direction: column;
  padding: 16px 15px 17px 14.5px;

  background: rgba(white, 0.1);
  border-radius: 10px;
}
.label {
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 12px;
  color: rgba(white, 0.5);
  letter-spacing: 0.86px;
  text-transform: uppercase;
  mix-blend-mode: normal;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.valueWrapper {
}
.value {
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 16px;
  color: white;
  letter-spacing: 1.2px;
  line-height: 19px;
  overflow-wrap: break-word;
  margin-top: 6px;
  display: flex;
  flex-wrap: nowrap;
}
.currency {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 12px;
  color: rgba(white, 0.4);
  letter-spacing: 0.86px;
  text-transform: uppercase;
  padding-left: 6px;
}
