.statusAndLikesContent {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;

  font-family: "Gilroy";
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  gap: 10px;
  @media (max-width: 500px) {
    margin-top: 15px;
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
.status {
  position: relative;
  display: flex;
  align-items: center;
}
.statusBtn {
  height: 25px;
  width: 25px;
  cursor: pointer;
  img {
    height: 25px;
    width: 25px;
  }
  button {
    padding: 0;
  }
}
.icon {
  & * {
    fill: #ffffff;
  }
}
.text {
  height: 19px;
  margin-left: 10px;
}

.emoji {
  background: rgba(255, 255, 255, 0.65);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  position: absolute;
  z-index: 99999;
  top: 0;
  // display: none;
  width: 100%;
  height: 135px;

  img {
    cursor: pointer;
  }
}
.open {
  display: block;
}
.removeCursor {
  cursor: default;
  * {
    cursor: default;
  }
}
