.medalsBlockMedalsBlock {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.sliderWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow {
  color: white;
  position: absolute;
}

.arrowLeft {
  @extend .arrow;
  left: -1%;
}

.arrowRight {
  @extend .arrow;
  right: -1%;
}

.item {
  width: 100%;
  height: 100%;
  cursor: pointer;
  img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
}
