$size: 75px;
$mobileSize: 55px;
.title {
  color: white;
  margin-bottom: 10px;
  opacity: 0.8;
}
.topFavoriteGamesSection {
  padding: 15px;
  display: flex;
  width: 100%;
  row-gap: 15px;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
}
.gameName {
  display: inline-block;
  min-height: 24px;
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  color: #ffffff;

  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;

  @media (max-width: 600px) {
    font-size: 18px;
  }
}
.itemContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: $size;

  & > div {
    height: 100%;
  }

  @media (max-width: 530px) {
    flex-direction: column;
  }
}
.textInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  color: white;

  max-width: 250px;
  min-width: 180px;
  width: 100%;
  @media (max-width: 530px) {
    max-width: 100%;
  }
}

.gameDescription {
  font-family: "Gilroy";
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #ffffff;
  height: 100%;
  width: 100%;

  // display: -webkit-box;
  // -webkit-line-clamp: 3;
  // -webkit-box-orient: vertical;
  // overflow: hidden;
  // text-overflow: ellipsis;
}
.gameInfo {
  gap: 10px;
  display: flex;

  align-items: center;
  @media (max-width: 530px) {
    width: 100%;
  }
}
.img {
  width: $size;
  height: $size;
  border-radius: 10px;
  @media (max-width: 530px) {
    width: $mobileSize;
    height: $mobileSize;
  }
}
.wageredInfo {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 5px;

  @media (max-width: 530px) {
    flex-direction: row;
    margin-top: 10px;
    align-self: flex-start;
    margin-left: calc(#{$mobileSize} + 10px);
  }
}
.wageredInfoTitle {
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;

  color: #ffffff;
  @media (max-width: 600px) {
    font-size: 14px;
  }
}
.wageredInfoValue {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;

  color: #34e7ff;

  @media (max-width: 600px) {
    font-size: 18px;
  }
}
