.likes {
  display: flex;
  align-items: center;
  height: 19px;
}
.liked {
  svg {
    overflow: visible;
  }
  path {
    fill: #ed3900;
    stroke: #ed3900;
  }
}
.btn {
  width: 21px;
  svg {
    overflow: visible;
  }
}
.countLike {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  margin: 0 4px 0 10px;
  text-align: center;
}
