.contentFooter {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  height: 32px;
}
.totalPape {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #ffffff;
  @media (max-width: 450px) {
    font-size: 12px;
  }
}
.currentPage {
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;

  color: #ffffff;

  @media (max-width: 450px) {
    font-size: 14px;
  }
}
.arrows {
  display: flex;
  gap: 5px;
}
.button {
  height: 32px;
  width: 32px;
  border-radius: 100%;
  background: rgba(0, 0, 0, 0.35);

  padding: 0;
}
