$arrowSize: 10px;
$background: white;

.root {
  background: $background;
  border-radius: 4px;
  padding: 8px;
  width: 100%;
  max-width: fit-content;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  transition: transform 0.4s, max-width 0.4s;
}
.text {
  margin: 0;
  color: #22242d;
  font-family: "Gilroy";
  font-size: 0.75rem;
  line-height: 15px;
  text-align: center;
  font-weight: bold;
}
.arrow {
  width: 0;
  height: 0;
  border-left: calc(#{$arrowSize} * 0.75) solid transparent;
  border-right: calc(#{$arrowSize} * 0.75) solid transparent;
  border-top: #{$arrowSize} solid $background;

  position: absolute;
  bottom: calc(-#{$arrowSize} + 1px);
  left: calc(50% - #{$arrowSize});
}
