@import "sass/index";

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  gap: 30px;
}

.userLogo {
  width: 92px;
  height: 92px;
  position: relative;
}

.circle {
  display: flex;
  background: $m_secondary-main;

  border-radius: 50%;
}

.block {
  margin-top: spacing(2.25);
  padding: spacing(1.5);
  background: rgba(white, 0.1);
  border-radius: spacing(1);
}
.modalBackground {
  background: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 48.48%,
      rgba(3, 1, 4, 0.0001) 80.79%,
      #000000 100%
    ),
    #0a0d18;
  min-height: calc(100% - 64px);
  justify-content: flex-start;
}
.rootPadding {
  padding: 0 25px 40px;
}
.userInfoRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: 500px) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.userWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: spacing(2.5);
  margin-left: 20px;
  @media (max-width: 500px) {
    margin-left: 0px;
    align-items: center;
  }
  @media (max-width: 390px) {
    padding-left: 0;
    margin-top: 20px;
    align-items: center;
  }
}

.user {
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 16px;
  color: white;
  letter-spacing: 0;
  line-height: 19px;
  text-transform: uppercase;

  margin-bottom: 7px;
}

.userSkeleton {
  padding: 4px 0;
}

.ratingWrapper {
}

.button {
  white-space: nowrap;

  &:not(:first-child) {
    margin-left: spacing(1.25);
  }
}

.loadingWrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.rewardImg {
  position: absolute;
  right: -25%;
  bottom: -10%;

  & > svg {
    height: 70px;
    width: 70px;
  }
}

.select {
  width: fit-content;
  background-color: transparent;

  padding-right: 5px;
  padding-left: 0;

  margin: 10px 26px 10px 8px;

  &Label {
    & > span {
      font-weight: bold;
      font-size: 18px;
    }
    padding-right: 24px;
  }

  &Icon {
    transform: scale(0.6);
  }

  &Item {
    background: #2d243d;
    color: white;
    &:hover {
      background-color: rgba(black, 0.1);
    }
    &Selected {
      border: 1px solid rgba(72, 53, 234, 0.6);
      border-radius: 8px;
      & > span {
        font-weight: bold;
      }
    }
  }

  &Options {
    background: #2d243d;
    padding: 5px;
  }

  // & > div {
  //   background: transparent !important;
  //   color: white;
  //   box-shadow: none !important;
  // }

  // & > svg * {
  //   stroke: white;
  // }
}

.optionsRoot {
  & > div {
    background: #2d243d;
  }
  & ul {
    background: #2d243d;
    padding: 5px;
  }
}

.optionElement {
  color: white;
  padding: 7px 20px;
  font-weight: 300;
}

.optionSelectedElement {
  font-weight: bold;
  border-radius: 8px;
  border: 1px solid rgba(72, 53, 234, 0.6);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
}
.userInfoContainer {
  display: flex;
}
