.section {
  padding: 15px;
  display: flex;
  flex-direction: column;
  width: 100%;
  row-gap: 15px;

  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
}
.sectionContainer {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.title {
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;

  color: white;
}
