$rowHeight: 18px;
.title {
  color: white;
  margin-bottom: 10px;
  opacity: 0.8;
}
.contentTable {
  display: grid;
}
.tableBody {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  justify-content: space-between;
  gap: 16px;
}
.start,
.end,
.center {
  display: flex;
  align-items: flex-start;
}
.start {
  justify-content: flex-start;
}
.end {
  justify-content: flex-end;
}
.center {
  justify-content: center;
}
.column {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: $rowHeight;
  gap: 17px;
}
.cell {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;

  color: #ffffff;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  @media (max-width: 450px) {
    font-size: 10px;
  }
}
.prize {
  display: flex;
  height: 100%;
  width: 100%;
  gap: 10px;
  align-items: center;
  p {
    margin: 0;
    padding: 0;
    letter-spacing: 1px;
  }
}
.coinImg {
  height: 100%;
  width: auto;
}
