@import "sass/index";

.wrapper {
  display: flex;
  flex-direction: column;

  width: 100%;
}
.favouriteGamesWrapper {
  margin-top: 20px;
  & > div {
    border-radius: 8px;
  }
}

.userLogo {
  width: 70px;
  height: 70px;
  position: relative;
}

.circle {
  display: flex;
  background: $m_secondary-main;

  border-radius: 50%;
}

.userInfoRow {
  display: flex;
  flex-direction: row;

  @media (max-width: 390px) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.userWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: spacing(2.5);
  margin-left: 20px;

  @media (max-width: 390px) {
    padding-left: 0;
    margin-top: 20px;
    align-items: center;
  }
}

.user {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 20px;
  color: white;
  letter-spacing: 0;
  line-height: 28px;
  text-transform: uppercase;
}

.block {
  margin-top: spacing(2.25);
  padding: spacing(1.5);
  background: rgba(white, 0.1);
  border-radius: spacing(1);
  text-transform: uppercase;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}
