@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  padding: 15px 14px;

  background: rgba(white, 0.1);
  border-radius: 20px;

  position: relative;
}
.titleWrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}
.title {
  font-family: "Gilroy";
  font-size: 20px;
  line-height: 24px;
  // letter-spacing: 0.85px;
  font-weight: 700;
  color: white;
  margin: 0;
}
.link {
  display: flex;
  align-items: center;

  margin: 0;
  color: white;
  cursor: pointer;

  font-family: "Gilroy";
  text-decoration: none;
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;

  & svg {
    margin-left: 3.8px;
  }
}
